import React,{useState} from 'react'
import { Link, animateScroll as scroll } from "react-scroll";
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText,
  Button,
  CardLink,
  Card,
  CardTitle,
  CardText
} from 'reactstrap';
import logo from '../logo – 1.png'
import '../App.css'

const  Header=()=> {
    const [isOpen,setIsOpen] = useState(false)
    const [navBackground,setNavBackground] = useState("")
    // document.addEventListener("scroll", () => {
    //   const backgroundcolor = window.scrollY < 100 ? "red" : "blue";

    //   this.setState({ navBackground: backgroundcolor });
    // });
    const toggle = ()=>setIsOpen(!isOpen)
    return (
      <div  id="home" >

        <Navbar  expand="md" color="dark" className="navbar-dark fixed-top" style={{color:"#fff",opacity:".8",}}>
            <NavbarBrand><Link to="home" smooth={true} duration={1000}><img src={logo} 
            height='100px'  className="imgFluid"
            />
            </Link>
            </NavbarBrand>
            <NavbarToggler onClick={toggle}/>
            <Collapse isOpen={isOpen} navbar>
            <Nav className="ml-auto" navbar >
            <NavItem>
            <NavLink style={{color:"#fff",fontSize:'20px',fontWeight:""}} ><Link to="details" smooth={true} duration={1000} onClick={toggle}>
                Details
                </Link></NavLink>
            </NavItem>
            <NavItem>
            <NavLink style={{color:"#fff",fontSize:'20px',fontWeight:""}} ><Link to="team"  smooth={true} duration={1000} onClick={toggle}>Team</Link></NavLink>
            </NavItem>
            <NavItem>
              <NavLink style={{color:"#fff",fontSize:'20px',fontWeight:""}} ><Link to="footer"  smooth={true} duration={1000} onClick={toggle}>Join Us</Link></NavLink>
            </NavItem>
            </Nav>
            </Collapse>

        </Navbar>
        
       
    
    </div>
       
        
        
      

    )
}


export default Header;
